<template>
  <RouterView @markData="getData" :markDataResult="markDataResult" />
</template>

<script>
import { ref } from "vue";
export default {
  setup() {
    const markDataResult = ref({});
    const getData = (markData) => {
      markDataResult.value = markData;
    };
    return {
      getData,
      markDataResult,
    };
  },
};
</script>

